import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { InView } from "react-intersection-observer";
import Gallery from "./Gallery";
import texts from "../../constants/texts";
import colors from "../../constants/colors";

const styles = {
  componentDiv: {
    backgroundColor: colors.transparent,
    overflow: "hidden",
    height: "100%",
    width: "100%",
    display: "flex",
    position: "relative",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
    color: colors.white
  },
  title: {
    display: "block",
    width: "100%",
    fontSize: "64px",
    marginBottom: "120px",
    marginRight: "40%",
    textAlign: "right"    
  }
};

const FifthSection = ({ classes, offset }) => (

  <ParallaxLayer offset={offset} speed={0.1}>
    <InView
      as="div"
      threshold={0.5}
      onChange={inView =>
        (document.getElementById("root").style.background = colors.section5)
      }
    />

    <div className={classes.componentDiv}>
      <h1 className={classes.title}>{texts.S5_TITLE}</h1>
      <Gallery />
    </div>

  </ParallaxLayer>
);

FifthSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(FifthSection);
