/* eslint-disable no-unused-expressions */
/* eslint-disable no-return-assign */
import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { Parallax } from "react-spring/renderprops-addons";
import FirstSection from "./components/FirstSection";
import SecondSection from "./components/SecondSection";
import ThirdSection from "./components/ThirdSection";
import FourthSection from "./components/FourthSection";
import FifthSection from "./components/FifthSection";
import SixthSection from "./components/SixthSection";
import SeventhSection from "./components/SeventhSection";
import EighthSection from "./components/EighthSection";
import RightNavigation from "./components/RightNavigation";
import Utils from "./utils/utils";
import mobgenners from "./constants/mobgenners";

import "./App.css";

const styles = {
  parallax: {
    margin: "0"
  }
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleScroll = value => {
      this.parallax && this.parallax.scrollTo(value);
    };
    this.state = {
      currentPage: 1,
      keys: Utils.randomObject(mobgenners)
    };
    this.handleOnScroll = this.handleOnScroll.bind(this);
  }

  handleGoToPage = offset => {
    this.handleScroll(offset + 1);
    this.pageOnChange(offset);
  };

  pageOnChange = number => {
    this.setState({
      currentPage: number
    });
  };

  handleOnScroll() {
    const page = parseInt(this.parallax.current / this.parallax.space, 0);
    if (page !== this.parallax.offset) {
      this.parallax.offset = page;
      this.pageOnChange(page + 1);
    }
  }

  render() {
    const { currentPage, keys } = this.state;
    const { classes } = this.props;
    return (
      <React.Fragment>
        <div onScroll={this.handleOnScroll}>
          <Parallax
            className={classes.parallax}
            ref={ref => (this.parallax = ref)}
            pages={11}
            scrolling
          >
            <FirstSection offset={0} onGoToPage={this.handleGoToPage} />
            <SecondSection offset={1} />
            <ThirdSection offset={2} />
            <FourthSection offset={3} />
            <FifthSection offset={4} />
            <SixthSection offset={5} keys={keys} />
            <SeventhSection offset={8} />
            <EighthSection offset={9} />
          </Parallax>
          <RightNavigation
            currentPage={currentPage}
            onGoToPage={this.handleGoToPage}
          />
        </div>
      </React.Fragment>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object
};

export default withStyles(styles)(App);
