import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { InView } from "react-intersection-observer";
import CountUp from "react-countup";
import ImageItem from "./ImageItem";
import mobgenners from "../../constants/mobgenners";
import colors from "../../constants/colors";
import texts from "../../constants/texts";


const styles = {
  sectionDiv: {
    width: "100%",
    backgroundColor: colors.transparent
  },
  componentDiv: {
    backgroundColor: colors.transparent,
    width: "80%",
    margin: "12%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center"
  },
  textDiv: {
    width: "90%",
    marginLeft: "10%",
    fontWeight: "bold",
    fontSize: "32px",
    marginTop: "280px",
    marginBottom: "200px"
  },
  galleryDiv: {
    width: "100%",
    display: "flex",
    flexDirection: "row",
    flexWrap: "wrap"
  }
};

const SixthSection = ({ classes, keys, offset }) => (
  <ParallaxLayer
    offset={offset}
    factor={2.5}
    speed={0}
    className={classes.sectionDiv}
  >
    <InView as="div" threshold={0.3} onChange={inView => document.getElementById('root').style.background = colors.section6}></InView>

    <div className={classes.componentDiv}>
      <div className={classes.textDiv}>

      {/* PEOPLE */}
        <div className={classes.lineText}> 
          <CountUp start={0} end={texts.S6_LINE1_NUM}>
            {({ countUpRef, start }) => (
              <div>
                <InView as="div" threshold={0.3} onChange={start}></InView>
                <span ref={countUpRef} /> {texts.S6_LINE1_TXT1}
              </div>
            )}
          </CountUp></div>
        {/* Nationalites */}
          <div className={classes.lineText}> 
          <CountUp start={0} end={texts.S6_LINE2_NUM}>
            {({ countUpRef, start }) => (
              <div>
                <InView as="div" threshold={0.3} onChange={start}></InView>
                <span ref={countUpRef} /> {texts.S6_LINE2_TXT1}
              </div>
            )}
          </CountUp></div>
        {/* Dogs */}

          <div className={classes.lineText}> 
          <CountUp start={0} end={texts.S6_LINE3_NUM}>
            {({ countUpRef, start }) => (
              <div>
                <InView as="div" threshold={0.3} onChange={start}></InView>
                <span ref={countUpRef} /> {texts.S6_LINE3_TXT1}
              </div>
            )}
          </CountUp></div>
      </div>
      <div className={classes.galleryDiv}>
        {keys.map(i => (
          <ImageItem key={i} image={mobgenners[i]} />
        ))}
      </div>
    </div>
  </ParallaxLayer>
);

SixthSection.propTypes = {
  classes: PropTypes.object,
  keys: PropTypes.array,
  offset: PropTypes.number
};

export default withStyles(styles)(SixthSection);
