import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import colors from "../../constants/colors";
import { InView } from 'react-intersection-observer'



const styles = {
  componentDiv: {
    backgroundColor: colors.transparent,
    overflow: "hidden",
    height: "100%",
    width: "100%",
    display: "flex",
    position: "relative",
    alignItems: "center",
    justifyContent: "center"
  }
};

const SecondSection = ({ classes, offset }) => (

  
  <ParallaxLayer offset={offset} speed={0.1}>
      <InView threshold={0.5} as="div" onChange={inView => document.getElementById('root').style.background = colors.section2}>
      </InView>
      <div>
        Section 2
      </div>
  </ParallaxLayer>
);

SecondSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(SecondSection);
