import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { InView } from "react-intersection-observer";
import { Image } from "semantic-ui-react";
import texts from "../../constants/texts";
import colors from "../../constants/colors";
import logo from "../../assets/img/logo-01-bottom.png";

const styles = {
  componentDiv: {
    backgroundColor: colors.transparent,
    overflow: "hidden",
    height: "100%",
    width: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  },
  textTopDiv: {
    width: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
    fontSize: "42px",
    fontWeight: "bold"
  },
  textBottomDiv: {
    width: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    fontSize: "42px",
    fontWeight: "bold"
  },
  logoDiv: {
    marginTop: "140px",
    marginBottom: "140px",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  logo: {
    width: "50%"
  }
};

const EighthSection = ({ classes, offset }) => (
  <ParallaxLayer factor={2} offset={offset} speed={0.1}>
    <InView
      as="div"
      threshold={0.5}
      onChange={inView =>
        (document.getElementById("root").style.background = colors.section8)
      }
    />

    <div className={classes.componentDiv}>
      <div className={classes.textTopDiv}>
        <div>{texts.S8_LINE1_TXT1}</div>
        <div>{texts.S8_LINE2_TXT1}</div>
        <div>{texts.S8_LINE3_TXT1}</div>
      </div>
      <div className={classes.logoDiv}>
        <Image src={logo} className={classes.logo} />
      </div>
      <div className={classes.textBottomDiv}>{texts.S8_LINE4_TXT1}</div>
    </div>
  </ParallaxLayer>
);

EighthSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(EighthSection);
