import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { InView } from "react-intersection-observer";
import TextLoop from "react-text-loop";
import texts from "../../constants/texts";

import colors from "../../constants/colors";

const styles = {
  sectionDiv: {
    width: "100%",
    backgroundColor: colors.transparent
  },
  componentDiv: {
    backgroundColor: colors.transparent,
    overflow: "hidden",
    height: "68%",
    width: "90%",
    margin: "16% 5%",
    display: "flex",
    position: "relative",
    alignItems: "center",
    justifyContent: "space-between",
    flexDirection: "column"
  },
  mainText: {
    textAlign: "center"
  },
  addressText: {
    textAlign: "left",
    width: "100%"
  },
  normalText: {
    fontWeight: "bold",
    fontSize: "64px",
    width: "fit-content",
    margin: "0px"
  },
  selected: {
    color: colors.section7,
    backgroundColor: colors.black,
    padding: "2px 10px"
  },
  smallText: {
    fontWeight: "bold"
  }
};

const SeventhSection = ({ classes, offset }) => (
  <ParallaxLayer offset={offset} speed={0} className={classes.sectionDiv}>
    <InView as="div" threshold={0.5} onChange={inView => document.getElementById('root').style.background = colors.section7} />

    <div className={classes.componentDiv}>
      <div className={classes.mainText}>
        <div className={classes.normalText}>{texts.S7_LINE1_TXT1}</div>
        <div className={classes.normalText}>{texts.S7_LINE2_TXT1}</div>
        <div className={classes.smallText}>{texts.S7_LINE3_TXT1}</div>
      </div>
      <div className={classes.addressText}>
        <div>
          <p className={classes.normalText}>{texts.S7_LINE4_TXT1}</p>
          <p className={`${classes.normalText} ${classes.selected}`}>
            <TextLoop mask>
              <span className={classes.effectText}>{texts.S7_LINE5_TXT1A}</span>
              <span className={classes.effectText}>{texts.S7_LINE5_TXT1B}</span>
              <span className={classes.effectText}>{texts.S7_LINE5_TXT1C}</span>
            </TextLoop>
          </p>
          <p className={classes.normalText}>{texts.S7_LINE6_TXT1}</p>
          <p className={classes.normalText}>{texts.S7_LINE7_TXT1}</p>
        </div>
      </div>
    </div>
  </ParallaxLayer>
);

SeventhSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(SeventhSection);
