import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { InView } from "react-intersection-observer";
import colors from "../../constants/colors";
import Header from "../Header";
import TextComponent from "./TextComponent";
import LogoComponent from "./LogoComponent";

const styles = {
  componentDiv: {
    backgroundColor: colors.transparent,
    overflow: "hidden",
    width: "100%",
    height: "100%",
    display: "block",
    position: "relative",
  },
  contentDiv: {
    width: "90%",
    height: "90%",
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    paddingLeft: "20px"
  },
  mediumDiv: {
    display: "flex"
  },
  logoDiv: {
    marginRight: "10%"
  }
};

const FirstSection = ({ classes, offset, onGoToPage }) => (
  <ParallaxLayer offset={offset} speed={0.1}>
      <InView as="div" onChange={inView => document.getElementById('root').style.background = colors.section1}>
      </InView>
    <div className={classes.componentDiv}>
      <Header onGoToPage={onGoToPage} />
      <div className={classes.contentDiv}>
        <div className={classes.mediumDiv}>
          <TextComponent />
        </div>
        <div className={classes.logoDiv}>
          <LogoComponent />
        </div>
      </div>
    </div>
  </ParallaxLayer>
);

FirstSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number,
  onGoToPage: PropTypes.func
};

export default withStyles(styles)(FirstSection);
